@import url(https://fonts.googleapis.com/css2?family=Didact+Gothic&family=Open+Sans&display=swap);
/* setup begins */
html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
footer,
header,
nav,
section {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
figcaption,
figure,
main {
  display: block;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
dfn {
  font-style: italic;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
details,
menu {
  display: block;
}
summary {
  display: list-item;
}
canvas {
  display: inline-block;
}
template {
  display: none;
}
[hidden] {
  display: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* SETUP ENDS */

/* OVERALL STYLES */

*,
h1 {
  margin: 0;
  padding: 0;
}

/* change the base font to use rems*/
html {
  font-size: 62.5%;
}

/* remove the list items */
ul {
  list-style-type: none;
}

/* remove default colour of a */
a {
  color: inherit;
  text-decoration: none;
}

/* remove resize on the textarea */
textarea {
  resize: none;
}

/* add default button styles */
button:hover,
button:focus {
  cursor: pointer;
}

/* wrapper styles */
.wrapper {
  max-width: 1200px;
  width: 70%;
  margin: 0 auto;
}

/* body styles */
body {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
}

/* heading styles */
h1,
h2,
h3,
button,
.profile p,
.linkHeading p a {
  font-family: "Didact Gothic", sans-serif;
  letter-spacing: 0.1rem;
}

/* heading heirarchy */
h1 {
  font-size: 2.4rem;
}

/* HEADER STYLES */

/* overall header styles */
header {
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgb(150, 240, 183) 0%,
    rgb(118, 236, 246) 100%
  );
  padding: 20px 0;
}

/* flex the content */
header .wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/* flex the nav links */
header ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/* add spacing between them */
header li {
  margin: 0 20px;
}

/* style the buttons in the nav */
header button {
  background: transparent;
}

/* when signed in flex the extra div */
div.signIn {
  display: flex;
  flex-direction: row;
}

/* profile styles */
.profile {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.6rem;
  margin: 0 10px;
}

/* profile icon */
.profile img {
  max-width: 40px;
  max-height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  object-fit: contain;
}

/* new post button */
header .newPostButton {
  border: none;
  padding: 10px;
  transition: all 0.5s ease;
}

/* hover effects */
header .newPostButton:hover,
header .newPostButton:focus {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* button styles */
header .loginButton,
.moduleContent button.submit {
  padding: 10px 20px;
  border: 1px solid black;
  border-radius: 20px;
  transition: all 0.5s ease;
}

/* change background on hover */
header .loginButton:hover,
header .loginButton:focus,
.moduleContent button.submit:hover,
.moduleContent button.submit:focus,
.newComment button:hover,
.newComment button:focus {
  background-color: black;
  color: rgb(23, 207, 152);
}

/* module styles */
.moduleContainer {
  position: fixed;
  z-index: 800;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.609);
}

/* module content styles */
.moduleContent {
  position: relative;
  top: 15%;
  left: 0;
  border-radius: 20px;
  width: 600px;
  margin: 0 auto;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.171);
  padding: 50px 50px 40px;
  background-color: #fff;
  z-index: 9999;
}

/* close icon */
.closeModule {
  position: absolute;
  border: none;
  top: 15px;
  right: 30px;
  font-size: 2.5rem;
  color: black;
  transition: all 0.5s ease;
}

/* hover styles for close icon */
.closeModule:hover,
.closeModule:focus {
  color: rgb(23, 207, 152);
}

/* h3 styles */
.moduleContent h3 {
  font-size: 3rem;
  margin-bottom: 20px;
  color: rgb(23, 207, 152);
}

/* label styles */
.moduleContent label {
  margin: 5px 0;
}

/* input styles */
.moduleContent input {
  margin-bottom: 10px;
  height: 35px;
  border: 0.5px solid rgb(107, 107, 107);
}

/* text area styles */
.moduleContent textarea {
  border: 0.5px solid rgb(107, 107, 107);
  height: 150px;
}

/* add spaces to create room for typed text */
.moduleContent input,
textarea {
  padding: 10px;
}

/* add focus styles */
.moduleContent input:focus,
textarea:focus,
.newComment input:focus {
  border-color: rgb(16, 190, 138);
}

/* styles for the submit buttons in modules */
.moduleContent button.submit {
  align-self: center;
  margin-top: 30px;
}

/* new post form styles */
.moduleContent form {
  display: flex;
  flex-direction: column;
}

.moduleContent .exitNewPost {
  border: none;
  font-weight: bold;
  font-size: 4rem;
  position: absolute;
  right: 10%;
  color: black;
  transition: all 0.5s;
}

.moduleContent .exitNewPost:hover,
.moduleContent .exitNewPost:focus {
  color: rgb(118, 236, 246);
}

/*      ARTICLES  */
main {
  z-index: 1;
  position: relative;
}

/* basic article styles */
.article {
  margin: 10px 0;
  padding: 10px 0 20px;
  border-bottom: 0.5px solid rgb(16, 190, 138);
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 1;
}

/* no border on last one */
.article:last-of-type {
  border-bottom: none;
}

/* the likes styles */
.likesSection {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* the likes button */
.likesSection button {
  border: none;
  font-size: 2rem;
  padding: 5px;
  background-color: transparent;
}

/* like button when clicked */
.likesSection button.liked {
  color: rgb(16, 190, 138);
}

/* likes numbers */
.likesSection p {
  color: rgb(168, 168, 168);
  font-size: 1.4rem;
}

/* make article info relative */
.articleInfo {
  position: relative;
}

/* article info headings styles */
.articleInfo .linkHeading {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media (max-width: 750px) {
  .articleInfo .linkHeading {
    flex-direction: column;
  }
}

/* external link */
.linkHeading p a {
  font-size: 1.4rem;
  color: rgb(168, 168, 168);
}

/* hover and focus styles */
.linkHeading p a:hover,
.linkHeading p a:focus {
  text-decoration: underline;
}

/* article info styles */
.articleInfo h2 {
  color: rgb(16, 190, 138);
  text-transform: capitalize;
  font-size: 2rem;
  margin-right: 10px;
}

/* addspacing for description */
.articleInfo .description {
  margin: 10px 0;
  color: rgb(109, 109, 109);
}

/* show comments button */
.articleInfo .showComments {
  border: none;
  background-color: transparent;
  font-size: 1.4rem;
  color: rgb(168, 168, 168);
}

/* show comments button */
.articleInfo .showComments:hover,
.articleInfo .showComments:focus {
  text-decoration: underline;
}

/* comments styles */
.comments {
  display: none;
  position: absolute;
  background-color: #fff;
  width: 600px;
  z-index: 10;
  padding: 30px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.171);
  border-radius: 10px;
}

/* show the comments when opened */
.comments.opened {
  display: block;
}

/* comments styles */
.comment {
  display: flex;
  width: 95%;
  margin: 0 auto;
  flex-direction: row;
  align-items: center;
  padding: 30px 10px;
  border-bottom: 0.5px solid rgb(202, 200, 200);
}

/* remove the bottom from the border */
.comment:last-of-type {
  border-bottom: none;
}

/* profile icon */
.comment svg {
  font-size: 2rem;
  color: rgba(73, 214, 172, 0.548);
  margin-right: 20px;
}

/* the new comment section */

/* form styles */
.newComment {
  display: flex;
  flex-direction: row;
  padding: 0 10px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/* input styles */
.newComment input {
  height: 35px;
  padding: 10px;
  width: 90%;
  border: 0.5px solid rgb(107, 107, 107);
}

/* add comment button */
.newComment button {
  border: none;
  background-color: transparent;
  width: 35px;
  height: 35px;
  border: 1px solid black;
  border-radius: 50%;
  transition: all 0.5s ease;
}

/* no comment p */
.comments .noComment {
  padding: 0 10px;
  margin-bottom: 20px;
}

/* user avatar */

.userContainer {
  min-width: 60px;
  max-width: 60px;
  padding: 10px;
}

.userContainer img {
  object-fit: contain;
  width: 100%;
  display: block;
  border-radius: 50%;
}

footer {
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgb(150, 240, 183) 0%,
    rgb(118, 236, 246) 100%
  );
  height: 80px;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  padding-top: 10px;
}

footer .wrapper {
  font-size: 1rem;
  display: flex;
}

.mintyCreators {
  width: 65%;
}
.mintyLinks {
  width: 35%;
}
.mintyLinks div {
  display: flex;
  flex-direction: column;
}

.mintyTeams {
  display: flex;
  justify-content: space-between;
}

.mintyTeams div {
  width: 50%;
  display: flex;
  flex-direction: column;
}

